.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(
        45deg,transparent,var(--color-primary),transparent
    );
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    margin-top: 7rem;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
    /* border: 2px solid red; */
} 

.about__card{
    gap: 30px;
    margin: 10px;
    
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    /* border: 2px solid red; */
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about__card:hover{
background: transparent;
border-color: var(--color-primary-variant);
cursor: default;
}
.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about__card h5{
    font-size: 0.95rem;
}
.about__card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
@media screen and (max-width:1024px) {
    .about__container{
        grid-template-columns: repeat(1,1fr);
        gap: 60px;
        /* margin-top: 100px; */
    }
    .about__me{
        width: 50%;
        margin: 3rem auto 4rem;
    }

    .about__content p{
        margin: 3rem 0 1.5rem;
    }
    .about__card{
        /* grid-template-columns: repeat(3,1fr); */
        /* margin-top: 2rem; */
        position: relative;
        top: 30px;
    }
}
@media screen and (max-width:600px) {
    .about__me{
        width: 65%;
        margin: 1rem auto 3rem;
    }
    .about__card{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
        margin-top: 3rem;
    }
    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 2.5rem 0;
    }
}