header{
    /* height: 100vh; */
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* cta */

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* Socials */

.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 10rem;
    /* size: 300px; */
    font-size: 30px;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


/* imagee profile */
.me{
   /* height: 1000px; */
    /* background: linear-gradient(var(--color-primary),transparent); */
    width: 22rem;
    /* height: rem; */
    position: relative;
    bottom: 130px;
    left: calc(50%-11rem);
    /* display: flex; */
    /* margin-left: 30%; */
    margin-top: 4.5rem;
    /* align-items: center; */
    /* justify-content: center; */
    text-align: center;
    /* border-radius: 12rem 12rem 0 0; */
    border-radius: -10px;
    overflow: hidden;
    padding: 5rem 1.5rem 0.5rem 1.5rem;
}
.me>img{
    /* height: 100%; */
    position: relative;
    bottom: 240px;
    /* right: 25px; */
    border-radius: 10px;
    width: 100%;
}
.me2{
    /* text-align: center; */
    /* justify-content: center;
     */
     /* height: 1000px; */
     background: linear-gradient(var(--color-primary),transparent);
     display: flex;
     height: 100%;
     border-radius: 90px;
    margin-top: 4rem;
     justify-content: center;
}

/* scroll down */

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 25rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


@media screen and (max-width:1024px) {
    header{
        height:108vh;
    }
    .header__socials,
    .scroll__down{
        display: flex;
        flex-direction: column;
    }
}
@media screen and (max-width:600px) {
    header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down{
        display: none;
    }
}

